.c-textarea {
  width: 100%;
  max-width: 560px;
  height: 145px;
  margin: 0;
  padding: .2em 1em;
  border: 1px solid #979797;
  border-radius: 3px;
  box-shadow: 0 1px 4px rgba(0,0,0,.1);
  background-color: #fff;
  color: inherit;
  font-family: inherit;
  font-weight: inherit;
  font-size: inherit;
  box-sizing: border-box;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}
.c-textarea::placeholder {
  color: #B5B4B4;
}
.c-textarea:-ms-input-placeholder {
  color: #B5B4B4;
}
.c-textarea::-ms-input-placeholder {
  color: #B5B4B4;
}
.c-textarea:focus {
  outline: 0;
  box-shadow: 2px 2px 6px rgba(0,0,0,.3);
}